import { Map } from 'immutable';
const image = Map({
    mongodb: require('assets/logo/mongodb.png'),
    crawl: require('assets/logo/crawl.png'),
    arduino: require('assets/logo/arduino.png'),
});
export default class ImageRepository {
    static get(key) {
        return image.get(key);
    }
}



