export default class ChartDatum {
    constructor(param) {
        this.param = param;
    }
    get key() {
        return this.param.key;
    }
    get color() {
        return this.param.color;
    }
    get values() {
        return this.param.values;
    }
    get toObject() {
        return {
            key: this.key,
            color: this.color,
            values: this.values.toArray(),
        };
    }
}



