import * as React from 'react';
import { Component } from 'react';
export default class PleaseScroll extends Component {
    constructor() {
        super();
        this.state = { opacity: 0 };
    }
    render() {
        const { className } = this.props;
        return (React.createElement("div", {"className": "module-please " + className, "style": this.state}, this.props.children));
    }
    componentDidMount() {
        if ('ontouchstart' in window) {
            // @todo do not show on touch device
            return;
        }
        const timeByShow = this.props.timeByShow || 1500;
        setTimeout(() => this.setState({ opacity: 1 }), timeByShow);
        this.setState({ opacity: 0 });
    }
}



