import ChartData from 'domains/ChartData';
import ChartDatum from 'domains/ChartDatum';
import ChartValue from 'domains/ChartValue';
import { List } from 'immutable';
export default class ChartDataFactory {
    static createBySkillList(skills) {
        return new ChartData(List.of(new ChartDatum({
            key: '',
            color: '#fff',
            values: skills
                .map(skill => {
                return new ChartValue({
                    label: skill.name,
                    value: skill.experience,
                    source: skill,
                });
            })
                .sortBy(row => -row.value)
                .toList()
        })));
    }
    static createByTagList(tags) {
        return new ChartData(List.of(new ChartDatum({
            key: '',
            color: '#fff',
            values: tags
                .map(tag => {
                return new ChartValue({
                    label: tag.name,
                    value: tag.experience,
                    source: tag,
                });
            })
                .sortBy(row => -row.value)
                .toList()
        }), new ChartDatum({
            key: '',
            color: '#999',
            values: tags
                .map(tag => {
                return new ChartValue({
                    label: tag.name,
                    value: tag.interest,
                    source: tag,
                });
            })
                .toList()
        })));
    }
}



